/* this should be a css reset */

html {
  height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)),
    url(/weather-app/static/media/weather.347d25f4.jpg) no-repeat center center fixed;
  background-size: cover;
}

.container {
  font-family: Arial, Helvetica, sans-serif;
  color: azure;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.alert {
  font-size: 1.4rem;
  font-weight: 800;
}

.large {
  font-size: 2rem;
}

.bigger {
  font-size: 1.5rem;
}

.small-text {
  font-size: 0.9rem;
}

.yellow {
  color: rgb(250, 250, 210);
}

/* form header */

.form-area {
  min-height: 100px;
  width: 100%;
  background-color: rgba(96, 96, 96, 1);
  display: grid;
  grid-template-columns: 1fr minmax(300px, 1fr) 1fr;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  justify-items: center;
  align-items: center;
}

form {
  display: flex;
}

.logo {
  font-family: 'Righteous', Arial, Helvetica, sans-serif;
  font-size: 2.5rem;
  font-weight: 800;
}

input[type='text'] {
  padding: 10px;
  color: rgba(96, 96, 96, 1);
  margin-right: 5px;
  border: 0 none;
  border-radius: 5px;
}

button.search {
  padding-bottom: 0;
  background-color: rgba(96, 96, 96, 1);
  color: white;
  font-size: 1.2rem;
  border: 0 none;
  cursor: pointer;
}

button:hover {
  color: rgb(170, 170, 170);
}

/* rule to hide placeholder on focus */

::-webkit-input-placeholder {
  opacity: 1;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
} /* Chrome <=56, Safari < 10 */
:-moz-placeholder {
  opacity: 1;
  -moz-transition: opacity 0.5s;
  transition: opacity 0.5s;
} /* FF 4-18 */
::-moz-placeholder {
  opacity: 1;
  -moz-transition: opacity 0.5s;
  transition: opacity 0.5s;
} /* FF 19-51 */
:-ms-input-placeholder {
  opacity: 1;
  -ms-transition: opacity 0.5s;
  transition: opacity 0.5s;
} /* IE 10+ */
::-webkit-input-placeholder {
  opacity: 1;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
:-ms-input-placeholder {
  opacity: 1;
  -ms-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
::placeholder {
  opacity: 1;
  transition: opacity 0.5s;
} /* Modern Browsers */

*:focus::-webkit-input-placeholder {
  opacity: 0;
} /* Chrome <=56, Safari < 10 */
*:focus:-moz-placeholder {
  opacity: 0;
} /* FF 4-18 */
*:focus::-moz-placeholder {
  opacity: 0;
} /* FF 19-50 */
*:focus:-ms-input-placeholder {
  opacity: 0;
} /* IE 10+ */
*:focus::-webkit-input-placeholder {
  opacity: 0;
}
*:focus:-ms-input-placeholder {
  opacity: 0;
}
*:focus::placeholder {
  opacity: 0;
} /* Modern Browsers */

/* unit switch */

.temp-switch {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 57px;
  height: 30px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgb(160, 160, 160);
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: '';
  height: 23px;
  width: 23px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: rgb(160, 160, 160);
}

input:focus + .slider {
  box-shadow: 0 0 1px rgb(160, 160, 160);
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 23px;
}

.slider.round:before {
  border-radius: 50%;
}

.temp-letters {
  z-index: 2;
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  position: relative;
  top: 7px;
  left: 10px;
  color: rgba(96, 96, 96, 1);
  font-size: 12px;
}

/* form error box */

.error-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
  padding: 10px;
  min-width: 300px;
  min-height: 120px;
  border: 0;
  border-radius: 5px;
  background-color: rgba(96, 96, 96, 0.6);
}

.error-box > * {
  padding: 5px;
}

/* content container */

.content-container {
  width: 80%;
  margin: auto 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  align-items: start;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.weather-current {
  min-height: 300px;
  margin-top: 20px;
  padding: 5px;
  background-color: rgba(96, 96, 96, 0.7);
  display: grid;
  border-radius: 5px;
  grid-template-columns: 1fr;
  grid-row-gap: 5px;
  row-gap: 5px;
}

.weather-current > * {
  padding-left: 1rem;
  /* border: 2px solid green; */
}

.city-name {
  padding-top: 10px;
  display: flex;
}

.city-box {
  padding-right: 1rem;
}

.main-info-box {
  display: flex;
  align-items: center;
}

.current-temp {
  width: 130px;
  font-size: 2.8rem;
}

.extra-box-1 > * {
  padding-bottom: 2px;
}

.extra-box-2 {
  display: flex;
}

.humidity {
  padding-left: 5px;
}

.sunrise-sunset {
  display: flex;
  padding-bottom: 10px;
}

.sunset {
  padding-left: 15px;
}
/* hours section */

.hours-container {
  grid-column: span 2;
  margin-top: 20px;

  background-color: rgba(96, 96, 96, 0.7);
  border-radius: 5px;
}

.hours-header {
  display: flex;
  justify-content: center;
  padding: 1rem;
}

.hours-roll {
  max-height: 650px;
  overflow: auto;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent; /* make scrollbar transparent */
}

.hour-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  grid-row-gap: 10px;
  row-gap: 10px;
  padding: 1rem;
}

.hour-row:nth-child(odd) {
  background-color: rgba(96, 96, 96, 0.7);
}

.hour-row:nth-child(even) {
  background: rgba(96, 96, 96, 0.5);
}

.hour-row > * {
  padding-right: 5px;
}

.hour-box {
  font-size: 2rem;
}

@media screen and (max-width: 600px) {
  .content-container {
    width: 90%;
  }
  .hours-container {
    grid-column: span 1;
  }

  .hours-roll {
    max-height: none;
  }
}

/* footer */

.footer {
  position: fixed;
  height: 40px;

  bottom: 0px;
  left: 0px;
  right: 0px;
  margin-bottom: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.static-bar {
  background-color: rgba(96, 96, 96, 1);
  color: #edeae5;
}

.static-bar a {
  color: #edeae5;
}

.static-bar a:hover {
  color: #afaca6;
}

@media screen and (max-width: 1000px) {
  .form-area {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-row-gap: 5px;
    row-gap: 5px;
    padding: 15px;
  }
  .content-container {
    width: 90%;
  }

  .hours-roll {
    max-height: none;
  }
}

@media screen and (min-width: 1600px) {
  .content-container {
    width: 70%;
  }
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

